.hero {
  justify-content: center;
  padding-top: 51px;
  padding-bottom: 40px;
  display: flex;
}

.dish-img {
  object-fit: cover;
  border-radius: 15px;
}

.btn-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  padding-bottom: 38px;
  display: flex;
}

.btn-category {
  color: rgba(5, 5, 5, .2);
  background-color: var(--primary-white-color);
  cursor: pointer;
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  padding: 14px 18px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-category:hover, .btn-category:focus {
  color: #f8f8f8;
  background-color: #9bb537;
  border: none;
}

.not-found-wrapper {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.element-icon {
  width: 68px;
  height: 58px;
}

.element-description {
  text-align: center;
  max-width: 335px;
  margin-top: 24px;
  font-size: 14px;
  line-height: 18px;
}

.is-hidden {
  visibility: hidden;
  display: none;
}

